<template>
    <div>
        <div class="info">
            <h3 style="margin-bottom:10px;display:inline-block;"> <div class="redS" style="display:inline-block;"><span>{{selfInfo.case_code}}</span><span><el-button type="primary" @click="totalCaseFn('total')" size="mini">{{selfInfo.case_total}}</el-button></span><span>{{selfInfo.case_level}}</span><span><el-button type="primary" v-show="selfInfo.case_status!='3'" @click="changeLevelFn" size="mini"></el-button></span></div></h3>
            <el-button type="primary" size="mini" @click="caseLog"></el-button>
            <el-button type="primary" size="mini" @click="caseMemorandum"></el-button>
            <div class="coverLevel">
                <div class="average">
                    <span style="display: inline-block;width: 270px;">{{selfInfo.case_id_num}}</span>
                    <span style="display: inline-block;width: 220px;">{{selfInfo.case_mobile}}</span>
                    <i class="fa fa-phone" style="color:#09CC5B;cursor:pointer;" v-show="selfInfo.case_status!='3'" @click="callFn(selfInfo.case_mobile,'')"  title=""></i>
                    <!--<el-button  type="primary" v-show="selfInfo.case_status!='3'" @click="callFn(selfInfo.case_mobile,'')" size="mini"></el-button>-->
                    <span>{{selfInfo.case_home_address}}</span>
                    <i class="fa fa-envelope" style="color:#F5952D;cursor:pointer;" v-show="selfInfo.case_status!='3'"  @click="sendLetter(2,selfInfo.case_mobile,'')" title=""></i>
                   <!-- <el-button   v-show="selfInfo.case_status!='3'" type="success" @click="sendLetter(2,selfInfo.case_mobile,'')" size="mini"></el-button> -->
                   <i class="fa fa-eye" style="color:#62A1EF;cursor:pointer;" v-show="selfInfo.case_status!='3'"  @click="sendLetter(3,selfInfo.case_mobile,'')"  title=""></i>
                  <!-- <el-button  v-show="selfInfo.case_status!='3'"  type="danger" @click="sendLetter(3,selfInfo.case_mobile,'')" size="mini"></el-button>-->
                </div>
            </div>
            <div class="coverLevel">
               <span style="display: inline-block;width: 270px;">{{selfInfo.case_organization_name}}</span>
               <span style="display: inline-block;width: 220px;">{{selfInfo.case_work_phone}}</span>
                <i class="fa fa-phone" style="color:#09CC5B;cursor:pointer;" v-show="selfInfo.case_status!='3'"  @click="callFn(selfInfo.case_work_phone,'')"  title=""></i>
               <!--<el-button  type="primary" v-show="selfInfo.case_status!='3'"  @click="callFn(selfInfo.case_work_phone,'')" size="mini"></el-button>-->
               <span>{{selfInfo.case_work_address}} </span>
               <i class="fa fa-envelope" style="color:#F5952D;cursor:pointer;" v-show="selfInfo.case_status!='3'"  @click="sendLetter(2,selfInfo.case_work_phone,'')"  title=""></i>
               <i class="fa fa-eye" style="color:#62A1EF;cursor:pointer;" v-show="selfInfo.case_status!='3'" @click="sendLetter(3,selfInfo.case_work_phone,'')"  title=""></i>
               <!--<el-button  type="success" v-show="selfInfo.case_status!='3'"  @click="sendLetter(2,selfInfo.case_work_phone,'')" size="mini"></el-button>
               <el-button  v-show="selfInfo.case_status!='3'"  type="danger" @click="sendLetter(3,selfInfo.case_work_phone,'')" size="mini"></el-button>-->
            </div>
            <div class="coverLevel">
               <span style="display: inline-block;width: 255px;">:{{selfInfo.case_mobile1}} </span>
                <i class="fa fa-phone" style="color:#09CC5B;cursor:pointer;" v-show="selfInfo.case_status!='3'"  @click="callFn(selfInfo.case_mobile1,'')"  title=""></i>
               <!--<span><el-button  type="primary" v-show="selfInfo.case_status!='3'"  @click="callFn(selfInfo.case_mobile1,'')" size="mini"></el-button></span>-->
                <span style="display: inline-block;width: 220px;">{{selfInfo.case_mobile2}} </span>
                 <i class="fa fa-phone" style="color:#09CC5B;cursor:pointer;" v-show="selfInfo.case_status!='3'"  @click="callFn(selfInfo.case_mobile2,'')"  title=""></i>
                <!--<span><el-button  v-show="selfInfo.case_status!='3'" type="primary" @click="callFn(selfInfo.case_mobile2,'')" size="mini"></el-button></span>-->
            </div>
            <div class="info">
                <h3 style="margin-bottom:10px;"></h3>
                <div><span>{{selfInfo.case_money}}  </span><span>{{selfInfo.case_paid}}  </span><span>{{selfInfo.client}}  </span><span>{{selfInfo.case_exceed_limit}}  </span><span>{{selfInfo.case_interest}}  </span><span>{{selfInfo.case_back_date}}  </span></div>
            </div>
        </div>
    </div>
    </div>
    
</template>
<script>
export default {
    name:'divInfo',
    props:[
        'selfInfo'
    ],
    methods:{
        totalCaseFn(str){
            this.$emit('totalCaseFn',str)
        },
        changeLevelFn(){
            this.$emit('changeLevelFn')
        },
        callFn(val,num){
            this.$emit('callFn',val,num)
        },
        sendLetter(val1,val2,val3){
            this.$emit('sendLetter',val1,val2,val3)
        },
        caseLog(){
            this.$emit('caseLog')
        },
        caseMemorandum(){
            this.$emit("caseMemorandum")
        }
    }
}
</script>
<style scoped>

</style>





